<template>
  <div class="home">
    <div class="input">
      <input type="text" v-model="value" placeholder="请输入快递单号" @focus="isShowHistory=true" @blur="cancelHistory">
      <div class="close" @click="value=''"></div>
      <div class="search" @click="searchFn"></div>
      <div class="history" v-if="isShowHistory&&expressCode.length>0">
        <div class="historyItem" @click="chooseExpressCode(code)" v-for="code,index in expressCode" :key="index">{{code}}</div>
      </div>
    </div>

    <div class="searchResult">
      <h3>搜索结果</h3>
      <div class="content" v-if="list.length>0">
        <div class="header">
          <div class="time">
            <div class="text">时间</div>
            <div class="icon" @click="sort=!sort">
              <span class="topT" :class="{active:sort}"></span>
              <span class="bottomT" :class="{active:!sort}"></span>
            </div>
          </div>
          <div class="addr">地点和跟踪进度</div>
        </div>

        <div class="list">
          <div class="listItem" v-for="(item,i) in showlist" :key="i">
            <div class="liTime">
              <div class="date">{{item.time.split(' ')[0]}}</div>
              <div class="hour">{{item.time.split(' ')[1]}}</div>
            </div>
            <div class="midStyle">
              <div class="circle" :class="{active:(sort&&i==0)||((!sort)&&i==(showlist.length-1))}"></div>
              <div class="line"></div>
            </div>
            <div class="addrTxt">
              {{item.status}}
            </div>

          </div>
        </div>
      </div>
      <div v-else>
        <h5 style="color:#ccc;">尚无搜索结果</h5>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'

export default {
  name: 'Home',
  data: function () {
    return {
      value: "DPK391001645810",
      list: [],
      sort: true,
      isShowHistory: false,
      expressCode: []
    }
  },
  mounted () {
    this.expressCode = localStorage.expressCode ? JSON.parse(localStorage.expressCode) : [];

  },
  computed: {
    showlist: function () {
      if (this.sort) {
        return [...this.list]
      } else {
        return [...this.list].reverse()
      }
    }
  },
  methods: {
    // https://market.aliyun.com/products/57126001/cmapi021863.html?spm=5176.2020520132.101.2.43957218bE9jpa#sku=yuncode1586300000
    async searchFn () {
      let httpUrl = `https://wuliu.market.alicloudapi.com/kdi?no=${this.value}`
      let config = {
        headers: {
          "Authorization": "APPCODE c94545b66fe84762a69b0b488ebd9152"
        }
      }
      let res = await axios.get(httpUrl, config)
      this.list = res.data.result.list;
      // 将搜索记录保存到本地存储
      this.saveExpressHistory()
      console.log(res)
    },
    saveExpressHistory () {
      // 首先从本地存储里查看是否有存放的历史快递单号，如果有就用json解析出啦，没有就创建一个新的数组
      this.expressCode = localStorage.expressCode ? JSON.parse(localStorage.expressCode) : [];

      // 判断数组是否已存在数据
      if (this.expressCode.indexOf(this.value) == -1) {
        this.expressCode.push(this.value)
        localStorage.expressCode = JSON.stringify(this.expressCode)
      }
    },
    chooseExpressCode (code) {
      // console.log(123)
      this.isShowHistory = false;
      this.value = code;
      this.searchFn()
    },
    cancelHistory () {
      setTimeout(() => this.isShowHistory = false, 50)
    }
  }
}
</script>

